@import url(https://fonts.googleapis.com/css?family=Electrolize);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Need to figure out how to make inner-select, outer-select visible!!!! */
body {
  font-family: 'Electrolize', sans-serif;
  background-color: #404040;
  color: #04FE19;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.inner-container {
  display: flex;
  border: 5px solid #04FE19;
  border-radius: 10px;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 3em;
  text-align: center;
  background-color: gray; }
  .inner-container .pad-bank {
    width: auto;
    height: auto;
    display: grid;
    grid-template-columns: 10vw 10vw 10vw;
    grid-template-rows: 10vw 10vw 10vw;
    margin: 1em; }
  .inner-container .drum-pad {
    position: relative;
    float: left;
    width: 100px;
    height: 80px;
    margin-right: 10px;
    border-radius: 5px;
    padding-top: 35px;
    box-sizing: border-box;
    cursor: pointer; }
  .inner-container .controls-container {
    height: auto;
    display: grid;
    grid-column-start: 2;
    margin: 30px 10px;
    vertical-align: top; }
    .inner-container .controls-container .control {
      width: auto;
      margin: auto; }
      .inner-container .controls-container .control p {
        margin-bottom: 0; }
  .inner-container #display {
    width: auto;
    background-color: gray;
    margin: 15px auto;
    padding: 15px;
    box-sizing: border-box; }

.outer-select, .inner-container .controls-container .control .select {
  margin: auto;
  border: 1px solid black;
  width: 50px;
  height: 20px;
  padding: 1px;
  background-color: black; }

.inner-select, .inner-container .controls-container .control .select .inner {
  width: 23px;
  height: 19px;
  background: #00e1ff;
  border: 1px solid black;
  box-sizing: border-box;
  cursor: pointer; }

@media screen and (max-width: 650px) {
  .inner-container {
    -webkit-transform: scale(0.7);
            transform: scale(0.7); } }

